
.body {
    background-image: url("../../img/chart-background-img.jpg")
}

input {
    margin-top: 5px;
}

.textInput {
    border-radius: 10px;
    height: 30px;
}

fieldset {
    background: rgb(173, 216, 230);
    width: 50%;
    margin-left: 25%;
    margin-top: 10%;
    border-radius: 10px;
}

.center {
    margin-top: 25px;
    text-align: center;
}

label {
    font-family: "cursive";
}

#submit {
    background-color: rgb(2, 117, 216);
}

.space {
    margin: 0px;
    margin-top: 150px;
    padding: 0px;
    height: 50px;
}

table, th, td {
    border: 1px solid black;
    border-collapse: collapse;
}
 
td {
    text-align: center;
}

table {
    width: 100%;
}

button {
    background-color:rgb(145, 190, 230);
    border-radius: 10px;
    font-style: normal;

}

a:link, a:visited, a:hover, a:active {
    text-decoration: none;
    color: red;
}